import { useEffect, useState } from 'react';
import {
  UserListApi,
  UserBanApi,
  UserEditApi,
  UserDelApi,
} from 'utils/Api';
import { Table, Button, Popconfirm, message, Modal, Form, Input, InputNumber } from 'antd';
const { Column } = Table;

const User = () => {
  const [tableData, setTableData] = useState();
  const [visible, setVisible] = useState(false);
  const [form] = Form.useForm();

  useEffect(() => {
    UserListApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          id: item._id,
          _id: item._id.slice(0, 7),
          createdAt: item.createdAt.replace(/[TZ]/g, ' '),
          updatedAt: item.updatedAt.replace(/[TZ]/g, ' '),
        }))
      );
    });
  }, []);

  const ban = (record) => {
    UserBanApi(record.id).then((res) => {
      const swapArr = tableData.map((item) => item.id === record.id ? {...item, state: !item.state}: item );
      setTableData(swapArr);
		});
  };
  const del = (record) => {
    UserDelApi(record.id).then((res) => {
      const swapArr = tableData.filter((item) => item.id !== record.id);
      setTableData(swapArr);
    });
  };
  const edit = (record) => {
		form.setFieldsValue(record)
		setVisible(true)
	};

  const onFinish = (value) => {
    UserEditApi({...value }).then((res) => {
      message.success('保存成功');
      onCancel();
			const swapData = tableData.filter((item) => item.id !== res.data._id)
      setTableData([
        ...swapData,
        {
					...res.data,
          _id: res.data._id.slice(0, 7),
          id: res.data._id,
        },
      ]);
    });
  };

  const onCancel = () => {
    setVisible(false);
		form.resetFields()
  };

  return (
		<>
    <Table dataSource={tableData} rowKey="id">
      <Column title="序号" dataIndex="_id" />
      <Column title="姓名" dataIndex="nick" />
      <Column title="会员等级" dataIndex="level" />
      <Column title="手机号码" dataIndex="phone" />
      <Column
        title="推荐人"
        dataIndex="referrer"
        render={(_,record) => (record.referrer && <p>{record.referrer.phone}</p>) }
      />
      <Column title="直推人数" dataIndex="inferior" />
      <Column title="状态" dataIndex="state" render={(_,record) => (record.state ?<p>正常</p>:<p>已封停</p>)} />
      <Column title="钱包结余" dataIndex="blance" />
      <Column title="冻结金额" dataIndex="freeze" />
      <Column title="注册时间" dataIndex="createdAt" />
      <Column title="最后在线" dataIndex="updatedAt" />
      <Column
        title="操作"
        dataIndex=""
        render={(_, record) => (
          <div className="flex gap-1">
            <Popconfirm
              title={`你确定要${record.state ? '封禁' : '解封'}此用户?`}
              onConfirm={() => ban(record)}
              okType="link"
              cancelText="取消"
              okText="确认"
            >
              <Button type="link">{record.state ? '封禁' : '解封' }</Button>
            </Popconfirm>
            <Button type="link" onClick={() => edit(record)}>
              修改
            </Button>
            <Popconfirm
              title="你确定要删除此用户?"
              onConfirm={() => del(record)}
              okType="link"
              cancelText="取消"
              okText="确认"
            >
              <Button type="link">删除</Button>
            </Popconfirm>
          </div>
        )}
      />
    </Table>
			
      <Modal
        title="修改信息"
        centered
        visible={visible}
        onCancel={onCancel}
        width={1000}
        keyboard={false}
        maskClosable={false}
        footer={[]}
      >
        <Form
          labelCol={{ span: 4 }}
          form={form}
          name="control-hooks"
          onFinish={onFinish}
        >
          <Form.Item name="id" label="会员编号" >
            <Input disabled/>
          </Form.Item>
          <Form.Item name="nick" label="会员姓名">
            <Input />
          </Form.Item>
          <Form.Item name="blance" label="账户余额">
            <InputNumber disabled />
          </Form.Item>
          <Form.Item name="money" label="充值金额">
            <InputNumber min={0} />
          </Form.Item>
          <Form.Item wrapperCol={{ span: 4, offset: 4 }}>
            <Button type="link" htmlType="submit">
              保存
            </Button>
          </Form.Item>
        </Form>
      </Modal>
		</>
  );
};

export default User;
